.contacts {
  padding: 56px 0 30px 16px;
  box-sizing: border-box;
  width: 100%;
  max-width: 476px;
  place-self: center end;

  @include vp-1199 {
    max-width: none;
    width: 100%;
    padding: 56px 45px;
    place-self: start center;
  }

  @include vp-767 {
    padding: 51px 15px 57px;
  }
}

.contacts h2 {
  margin-top: 0;
  margin-bottom: 38px;

  @include vp-1199 {
    width: 74%;
    margin: 0 auto 25px;
  }

  @include vp-767 {
    width: 100%;
    margin-bottom: 21px;
  }
}

.contacts__list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1.1fr 1fr;
  grid-auto-flow: dense;
  column-gap: 21.9%;
  row-gap: 11px;

  @include vp-1199 {
    grid-template-columns: 1.2fr 1fr;
    width: 74%;
    margin: 0 auto;
    column-gap: 18%;
    row-gap: 15px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    width: 100%;
  }
}

.contacts__list::after {
  position: absolute;
  content: "";
  top: 6px;
  left: 50.8%;
  display: block;
  width: 1px;
  height: 84%;
  background-color: $color-light-slate-grey;

  @include vp-1199 {
    left: 53%;
    top: 8px;
    height: 87%;
  }

  @include vp-767 {
    display: none;
  }
}

.contacts__item {
  margin: 0;
  padding: 0;
  padding-left: 4px;

  @include vp-1199 {
    padding-left: 0;
  }

  &:nth-child(3n-1) {
    grid-column: 1/2;
  }
}

.contacts__item h3 {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;

  @include vp-1199 {
    font-size: 20px;
  }
}

.contacts__item address,
.contacts__item p,
.contacts__item a {
  margin: 0;
  padding: 5px 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  @include vp-1199 {
    font-size: 18px;
    line-height: 152.34%;
  }
}

.contacts__item a {
  display: block;
  width: max-content;

  @include hover-focus {
    color: $color-crimson;
  }

  &:active {
    opacity: 0.5;
  }
}
