.button {
  font-family: $ff-Roboto-Condensed;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 24px;
  background: transparent;
  color: $color-default-white;
  border: none;
  box-shadow: none;
  appearance: none;
  cursor: pointer;
  user-select: none;

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    border: 4px solid $color-default-white;
  }

  @include hover-focus {
    color: $color-resolution-blue;
    background-color: $color-default-white;
  }

  &:active {
    color: $color-default-black;
    opacity: 0.6;
  }
}

.button--secondary {
  color: $color-sapphire;
  padding: 24px 0 26px;

  &::after {
    border: 3px solid $color-sapphire;
  }
}

.button--promo {
  text-transform: uppercase;
  padding: 27px 24px 28px 24px;

  @include vp-1199 {
    padding: 17px 24px 13px;
    font-size: 10px;
    line-height: 12px;

    &::after {
      border-width: 2px;
    }
  }

  @include hover {
    outline: 4px solid $color-default-white;
    outline-offset: -4px;
  }

  @include focus {
    outline: 4px solid $color-default-white, 1px solid $color-cobalt;
    outline-offset: -4px, 4px;
  }
}
